.layout {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   gap: 50px;
   padding: 20px 80px;
}

@media (max-width: 1000px) {
   .layout {
      padding: 20px 30px;
   }
}

@media (max-width: 800px) {
   .layout {
      padding: 20px 10px;
   }
}
